<template>
  <el-dialog @close="resetInfo" :title="cateData.id>0?'编辑':'添加'" :visible.sync="info.isShow">
    <el-form ref="cateForm" :model="cateData" :rules="rules" label-width="80px">
      <el-form-item label="上级分类" prop="pid">
        <el-select v-model="cateData.pid" placeholder="请选择上级分类">
          <el-option label="顶级分类" :value="0"></el-option>
          <el-option
            v-for="item in cateList"
            :key="item.id"
            :label="item.catename"
            :value="item.id"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="分类名称" prop="catename">
        <el-input v-model="cateData.catename" placeholder="请输入分类名称"></el-input>
      </el-form-item>
      <el-form-item label="分类图片" prop="img" v-if="cateData.pid!==0">
        <!-- 
              action: 图片上传的接口
              list-type: 样式相关的属性
              on-preview: 图片预览的事件处理
              on-remove: 移除图片的事件处理
              auto-upload: 取消自动上传行为
              on-change: 监听用户选择
              file-list: 展示默认图片  
              limit: 最大允许上传个数
              :on-preview="handlePictureCardPreview"
        -->
        <el-upload
          action="#"
          list-type="picture-card"
          :on-remove="handleRemove"
          :auto-upload="false"
          :on-change="fileChange"
          :file-list="fileList"
          :limit="1"
          :on-exceed="limitTips"
        >
          <i class="el-icon-plus"></i>
        </el-upload>
        <el-dialog :visible.sync="dialogVisible">
          <img width="100%" :src="dialogImageUrl" alt />
        </el-dialog>
      </el-form-item>
      <el-form-item label="分类状态" prop="status">
        <el-radio-group v-model="cateData.status">
          <el-radio :label="1">正常</el-radio>
          <el-radio :label="2">禁用</el-radio>
        </el-radio-group>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="info.isShow = false">取 消</el-button>
      <el-button type="primary" @click="submitForm">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
import { mapState, getCateListActoin, mapActions } from "vuex"
export default {
  props: ["info"],
  computed: {
    ...mapState({ cateList: state => state.category.cateList })
  },
  data() {
    // 自定义表单校验方法
    const validImg = (rule, value, callback) => {
      if (this.cateData.img === "") {
        return callback(new Error("请选择图片"))
      }
      callback()
    }

    return {
      // 默认需要展示的图片数组  
      fileList:[],  
      // 和表单进行绑定的数据对象
      cateData: {
        // 待编辑分类的id: 默认数据类型必须为数值
        id:0,
        // 父级id
        pid: 0,
        // 分类名称
        catename: "",
        // 分类图片
        img: "",
        // 分类状态
        status: ""
      },
      // 表单校验
      rules: {
        pid: { required: true, message: "请选择上级分类", trigger: "blur" },
        catename: {
          required: true,
          message: "请填写分类名称",
          trigger: "blur"
        },
        status: { required: true, message: "请选择分类状态", trigger: "blur" }
        //   img:{validator:validImg,trigger:'blur'}
      },
      // 图片上传组件所需属性
      dialogImageUrl: "",
      dialogVisible: false
    }
  },
  methods: {
    ...mapActions("category", ["getCateListActoin"]),
    // 图片上传组件所需方法
    handleRemove(file, fileList) {
      console.log(file, fileList)
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url
      this.dialogVisible = true
    },
    // 监听用户选择的文件对象
    fileChange(file) {
      // 更新数据
      this.cateData.img = file.raw
    },
    // 上传文件数量超过限制的提示
    limitTips(){
      this.$error('最多只能上传一张图片')  
    },  
    // 表单提交
    submitForm() {
      this.$refs.cateForm.validate(valid => {
        if (valid) {
          // 提交表单
          // this.cateData=> FormData对象
          const fd = new FormData()
          for (let key in this.cateData) {
            fd.append(key, this.cateData[key])
          }
          const url=this.cateData.id>0?'/cateedit':'/cateadd'  
          this.$http.post(url, fd).then(res => {
            if (res.code === 200) {
              this.$success(res.msg)
              // 1-刷新列表视图
              this.getCateListActoin()
              // 2-关闭模态框
              this.info.isShow=false 
              // 3-重置表单 
              this.resetInfo()
            } else {
              this.$error(res.msg)
            }
          })
        }
      })
    },
    // 设置表单信息
    setInfo(obj){
        this.cateData={...obj}
        // 更新fileList默认图片数组
        this.fileList=obj.img!==''?[{url:this.$imgBaseUrl+obj.img}]:[]
    },
    // 重置信息
    resetInfo(){
        // 3-重置表单
        this.cateData={id:'',pid:0,catename:'',img:'',status:''}
        this.fileList=[]
    }
  }
}
</script>

<style>
</style>


