<template>
  <el-card>
    <el-button @click="info.isShow=true" type="primary" icon="el-icon-circle-plus-outline">新增</el-button>
    <el-divider></el-divider>
    <el-table
      :data="cateList"
      style="width: 100%;margin-bottom: 20px;"
      row-key="id"
      :tree-props="{children: 'children', hasChildren: 'hasChildren'}"
    >
      <el-table-column prop="id" label="ID" min-width="180"></el-table-column>
      <el-table-column prop="img" label="分类图片" min-width="180">
        <template v-slot="$props">
          <img style="height:80px" v-if="$props.row.img!==''" :src="$imgBaseUrl+$props.row.img" alt />
        </template>
      </el-table-column>
      <el-table-column prop="catename" label="分类名称" min-width="180"></el-table-column>
      <el-table-column prop="status" label="状态" min-width="180px">
        <template v-slot="$props">
          <el-tag type="success" v-if="$props.row.status===1">正常</el-tag>
          <el-tag type="danger" v-else>禁用</el-tag>
        </template>
      </el-table-column>
      <el-table-column label="操作" min-width="180px">
        <template v-slot="$props">
          <el-button @click="edit($props.row)" type="success" circle size="mini" icon="el-icon-edit"></el-button>
          <el-button @click="del($props.row.id)" type="danger" circle size="mini" icon="el-icon-delete"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 添加/编辑子组件 -->
    <Dialog :info="info" ref="dialog"/>
  </el-card>
</template>

<script>
import Dialog from "./Dialog";
// 导入vuex的辅助方法
import { mapState, mapActions } from "vuex";
export default {
  components: {
    Dialog
  },
  data(){
    return {
      // 即将传递给子组件的数据
      info:{
        // 控制子组件的显示状态
        isShow:false
      }
    }
  },
  computed: {
    ...mapState({ cateList: state => state.category.cateList })
  },
  methods: {
    ...mapActions("category", ["getCateListActoin"]),
    // 编辑
    edit(row){
      // 1-设置表单数据
      this.$refs.dialog.setInfo(row)
      // 2-显示表单
      this.info.isShow=true
    },
    // 删除
    del(id){
      this.$myConfirm(async ()=>{
        // 1-发请求, 删除数据
        const res =await this.$http.post('/catedelete',{id})
        // 2-更新数据列表
        if(res.code===200){
          this.$success(res.msg)
          return this.getCateListActoin()
        }
        this.$error(res.msg)
      })
    }
  },
  created() {
    this.getCateListActoin()
  }
}
</script>

<style>
</style>


